import React from "react";
import MovieCardItem from "../MovieCardItem/MovieCardItem";
import ButtonItem from "../ButtonItem/ButtonItem";
import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation, Pagination, Scrollbar, A11y } from "swiper/modules";
import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";
import "swiper/css/scrollbar";

const HorizontalListItem = ({ list, title,section, onViewMoreClick }) => {
  return (
    <section className="text-light d-flex flex-column justify-content-center align-items-center  w-100 gap-3 ">
      <div>
        <h2 className="satisfy-font align-self-center py-3">{title}</h2>
      </div>

      {/* <SwiperItem
        listItems={list.map((item) => (
          <SwiperSlide key={item.id}>
            <MovieCardItem movie={item} />
          </SwiperSlide>
        ))}
        onViewMoreClick={onViewMoreClick}
      /> */}

      <Swiper
        className=" d-flex justify-content-start align-items-center gap-3 w-100 p-3 rounded-2 shadow bg-dark-opacity"
        modules={[Navigation, Scrollbar]}
        // spaceBetween={10}
        // centeredslide={true}
        // slidesPerView={1}
        // width={300}
        breakpoints={{
          0: {
            width: 300,
            spaceBetween: 10,
          },
          320: {
            width: 300,
            spaceBetween: 10,
          },
          480: {
            width: 300,
            spaceBetween: 10,
          },
          640: {
            width: 300,
            spaceBetween: 10,
          },
          768: {
            width: 300,
            spaceBetween: 10,
          },
          992: {
            width: 300,
            spaceBetween: 10,
          },
          1200: {
            width: 300,
            spaceBetween: 10,
          },
          1400: {
            width: 300,
            spaceBetween: 10,
          },
        }}
        scrollbar={{ draggable: true }}
        // onSwiper={(swiper) => console.log(swiper)}
        // onSlideChange={() => console.log("slide change")}
      >
        {list.map((item) => (
          <SwiperSlide key={item.id}>
            <MovieCardItem movie={item} type={section} />
          </SwiperSlide>
        ))}

        <SwiperSlide>
          <ButtonItem
            // text={<BsThreeDots size={40} color="yellow" />}
            text={"View More"}
            className={"border-0 btn-outline-warning "}
            style={{ width: "260px", height: "400px" }}
            onClick={onViewMoreClick}
            // style={{ border: "1px solid yellow" }}
          />
        </SwiperSlide>
      </Swiper>

      {/* <div className="overflow-x-scroll d-flex justify-content-start align-items-center gap-3 w-100 p-3 pt-4 rounded-2 shadow bg-dark-opacity">
        {list.map((item) => (
          <MovieCardItem key={item.id} movie={item} />
        ))}

        <ButtonItem
          // text={<BsThreeDots size={40} color="yellow" />}
          text={"View More"}
          className={"border-0 btn-outline-warning "}
          style={{ width: "260px", height: "400px" }}
          onClick={onViewMoreClick}
          // style={{ border: "1px solid yellow" }}
        />
      </div> */}
    </section>
  );
};

export default HorizontalListItem;
